/* ===btn=== */
.btn {
  border: none;
  border-radius: 0.75rem;
  padding: 0.5rem 1rem;
  color: white;
  cursor: pointer;
}
.btn-primary {
  background-color: var(--primary);
  transition: all 0.25s ease;
}
.btn-primary:focus {
  box-shadow: 0 0 0 5px var(--primary-half);
}
.btn-primary:hover {
  background-color: var(--primary-light);
}
.btn-primary:active {
  background-color: var(--primary-dark);
}
.btn-secondary {
  background-color: var(--secondary);
  transition: all 0.25s ease;
}
.btn-secondary:focus {
  box-shadow: 0 0 0 5px var(--secondary-half);
}
.btn-secondary:hover {
  background-color: var(--secondary-light);
}
.btn-secondary:active {
  background-color: var(--secondary-dark);
}
.btn-success {
  background-color: var(--success);
  transition: all 0.25s ease;
}
.btn-success:focus {
  box-shadow: 0 0 0 5px var(--success-half);
}
.btn-success:hover {
  background-color: var(--success-light);
}
.btn-success:active {
  background-color: var(--success-dark);
}
.btn-danger {
  background-color: var(--danger);
  transition: all 0.25s ease;
}
.btn-danger:focus {
  box-shadow: 0 0 0 5px var(--danger-half);
}
.btn-danger:hover {
  background-color: var(--danger-light);
}
.btn-danger:active {
  background-color: var(--danger-dark);
}
.btn-warning {
  background-color: var(--warning);
  color: black;
  transition: all 0.25s ease;
}
.btn-warning:focus {
  box-shadow: 0 0 0 5px var(--warning-half);
}
.btn-warning:hover {
  background-color: var(--warning-light);
}
.btn-warning:active {
  background-color: var(--warning-dark);
}

.nav-list {
  list-style: none;
  margin: 0;
}
.nav-link {
  text-decoration: none;
  padding: 1rem 0.25rem;
}
.nav-link[aria-disabled=true] {
  color: var(--link-clr-light);
  cursor: not-allowed;
}
.nav-link[aria-disabled=true]:active {
  pointer-events: none;
}

@media (max-width: 0px) {
  .navbar-xs .navbar-toggler, .navbar-xs .navbar-toggler::before {
    display: block;
  }
  .navbar-xs .navbar-collapse {
    flex-basis: 100%;
  }
  .navbar-xs .navbar-collapse.collapse:not(.show) {
    display: none;
  }
  .navbar-xs .navbar-collapse::before {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    margin: 1rem 0;
    background-color: var(--light-clr);
  }
  .navbar-xs .container {
    align-items: center !important;
  }
  .navbar-xs .nav-list {
    flex-direction: column;
  }
  .navbar-xs .nav-list .nav-link {
    display: block;
    padding: 0.25rem 0.5rem;
    margin-bottom: 1rem;
    border-radius: 0.25rem;
    background-color: var(--nav-collapse-tab);
  }
  .navbar-xs .nav-list .nav-link:hover:not([aria-disabled=true]) {
    background-color: var(--nav-collapse-tab-light);
  }
}
