.navbar {
  --link-clr: var(--nav-link);
  --link-clr-light: var(--nav-link-light);
  --link-clr-lighter: var(--nav-link-lighter);
  --link-clr-dark: var(--nav-link-dark);
  --link-clr-darker: var(--nav-link-darker);
  --link-clr-half: var(--nav-link-half);
  border-bottom: 1px solid var(--light-clr);
}
.navbar .nav-link.active {
  --link-clr: var(--nav-link-lighter);
  --link-clr-light: var(--nav-link-lighter-light);
  --link-clr-lighter: var(--nav-link-lighter-lighter);
  --link-clr-dark: var(--nav-link-lighter-dark);
  --link-clr-darker: var(--nav-link-lighter-darker);
  --link-clr-half: var(--nav-link-lighter-half);
}
