@use "sass:color";
@use "../variables.scss" as *;
@use "../funtions.scss" as *;

@mixin variant($name, $color_var) {
  &-#{$name} {
    background-color: var($color_var);
    @if ($color_var == --warning) {
      color: black;
    }
    @include shadowed_focus(#{$color_var}-half);
    &:hover {
      background-color: var(#{$color_var}-light);
    }
    &:active {
      background-color: var(#{$color_var}-dark);
    }
  }
}

/* ===btn=== */
.btn {
  border: none;
  border-radius: 0.75rem;
  padding: 0.5rem 1rem;
  color: white;
  cursor: pointer;
  @each $name, $color_var in $variants_vars {
    @include variant($name, $color_var);
  }
}
